.stats {
    display: flex;
    /* padding: 20px; */
    justify-content: space-between;
    border-top: 1px solid #ccc;
}

.statsItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90px;
    padding-top: 20px;
    padding-bottom: 20px;
    background: #eee;
}

.statsItem:not(:last-child) {
    border-right: 1px solid #ccc;
}

.lable {
    display: block;
    margin-bottom: 10px;
}

.quantity {
    font-weight: 700;
}
